.header {
    background-image: url('./assets/img/background-homepage.jpg');
    background-size: cover;
    width: 100%;
    background-position: center;
    padding: 20px;
    .logo {
        max-width: 300px;
    }
}

.main {
    background-image: url('./assets/img/thankyoupage_bgimage.jpg');
    background-size: cover;
    .text_thankyou {
        background-color: rgba(2, 54, 98, 0.72);
        background-image: linear-gradient(to right, #2a72ae 0%, #2a72ae 100%), url('./assets/img/thankyoupage_bgimage.jpg');
        background-blend-mode: multiply;
        background-position: top;
        border-bottom: 5px solid $greenText;
        padding: 50px 0;
        border-top: 5px solid #2a72ad;
        .text_wrapper {
            display: block;
            width: 90%;
            max-width: 800px;
            margin: 0 auto;
            text-align: center;
            color: #ffffff;
            h1 {
                margin-bottom: 40px;
            }
            a {
                color: #ffffff;
                text-decoration: none;
            }
        }
    }
    .content_hub_selections {
        background-color: rgba(11, 88, 0, 0.75);
        background-image: linear-gradient(to right, #3aad2a 0%, #3aad2a 100%), url('./assets/img/thankyoupage_bgimage.jpg');
        background-blend-mode: multiply;
        background-position: bottom;
//        background-size: cover;
        padding: 50px 0;
        .hub_single {
            background-color: #fff; 
            display: block;
            width: 90%;
            max-width: 800px;
            margin: 0 auto;
            margin-bottom: 70px;
            position: relative;
            padding: 0;
            box-shadow: 0 10px 25px rgba(0,0,0,0.75);
//            min-height: 200px;
            .img {
                background-image: url('http://placehold.it/300x200');
                background-size: cover;
                background-position: center;
                width: 34%;
                display: block;
                text-align: left;
                height: 100%;
                min-height: 200px;
//                position: relative;
                img {
                    max-width: 100%;
                    display: block;
                    height: 100%;
                    margin: 0;
                }
            }
            .content {
                text-align: left;
                width: 65%;
                display: inline-block;
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                -ms-atransform: translateY(-50%);
                right: 0;
                padding: 10px;
                vertical-align: top;
                h2 {
                    font-size: 1rem;
                    margin-bottom: 10px;
                    margin-top: 0;
                }
                p {
                    font-size: .75rem;
                    margin-bottom: 10px;
                }
                a {
                    font-size: .75rem;
                    color: #000;
                    text-decoration: none;
                    text-transform: uppercase;
                    transition: all ease-out .25s;
                    display: inline-block;
                    &:hover {
                        transform: translateX(5px);
                        -ms-transform: translateX(5px);
                    }
                }
            }
        }
    }
}

.ie {
    .text_thankyou {
        background-image: none !important;
    }
    .content_hub_selections {
        background-image: none !important;
    }
}